import React,{useState} from "react";
import Modal from "../../components/modal";
import { Stack, Typography } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import { Validate } from "../../helpers/utils";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import Checkbox from '@mui/material/Checkbox';
import crossIcon from '../../assets/svg/cross-icon.svg'
import { t } from "i18next";

const Add = ({ open, onClose, data, setData, onSubmit, loading,edit,plantList,days, setDays,masterShift}) => {
  const USER_SCHEMA = [
    {
      title: "Name",
      placeholder: "Enter Name",
      type: SCHEMA_TYPES.INPUT,
      name: "name",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Plant",
      placeholder: "Select Plant",
      type: SCHEMA_TYPES.SELECT,
      name: "plant",
      validator: VALIDATORS.REQUIRED,
      keyValues: plantList,
    },
    {
      title: "Start Time",
      placeholder: "select time",
      type: SCHEMA_TYPES.TIME,
      name: "start_time",
      validator: VALIDATORS.REQUIRED,
    }, 
    {
      title: "End Time",
      placeholder: "select time",
      type: SCHEMA_TYPES.TIME,
      name: "end_time",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Type",
      // placeholder: "Select Plant",
      type: SCHEMA_TYPES.RADIO  ,
      name: "type",
      validator: VALIDATORS.REQUIRED,
      keyValues:[{"value":true,"label":"Working Shift"},{"value":false,"label":"Planned Break"}]
    },
    data.type === "false" &&({
      title: "Master Shift",
      placeholder: "Select Master Shift",
      type: SCHEMA_TYPES.SELECT,
      name: "swiftmaster",
      // validator: VALIDATORS.REQUIRED,
      keyValues:[{ key: '', value: 'None' },...masterShift]
    }),
   
  ];
  const [errors, setErrors] = useState(new Map());
  const label = { inputProps: { 'aria-label': 'Checkbox demo' } };
 
  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };
  const handleChangedays = (day) => (event) => {
    setDays((prev) => ({
      ...prev,
      [day]: event.target.checked,
    }));

    console.log("days--------",days)

  };

   const handleSubmit=async ()=>{
          const _errors = Validate(USER_SCHEMA, data);
          setErrors(_errors);
          if (_errors.size) return;
          await onSubmit()
          return;
    }
  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(700px,90vw)"
      hideCloseIcon={crossIcon}
        wrapperClasses="px-6"
    >
      <h3 className="opacity-80">Add Shift Schedule</h3>
      
      <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={USER_SCHEMA}
        />
        <Stack sx={{mt:1}}>
          <Typography>Week Days</Typography>
            <Stack sx={{flexDirection:'row',gap:1}}>
              <Checkbox onChange={handleChangedays('monday')} {...label} icon={<Typography>M</Typography>} checkedIcon={<Typography>M</Typography>} />
              <Checkbox  onChange={handleChangedays('tuesday')} {...label} icon={<Typography>T</Typography>} checkedIcon={<Typography>T</Typography>} />
              <Checkbox  onChange={handleChangedays('wednesday')}{...label} icon={<Typography>W</Typography>} checkedIcon={<Typography>W</Typography>} />
              <Checkbox onChange={handleChangedays('thursday')} {...label} icon={<Typography>T</Typography>} checkedIcon={<Typography>T</Typography>} />
              <Checkbox  onChange={handleChangedays('friday')}{...label} icon={<Typography>F</Typography>} checkedIcon={<Typography>F</Typography>} />
              <Checkbox onChange={handleChangedays('saturday')} {...label} icon={<Typography>S</Typography>} checkedIcon={<Typography>S</Typography>} />
              <Checkbox onChange={handleChangedays('sunday')}{...label} icon={<Typography>S</Typography>} checkedIcon={<Typography>S</Typography>} />
            </Stack>
        </Stack>
     
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
          <Button bordered gray onClick={onClose}>
          {t('cancel')}
        </Button>
        <Button color={'light_green'} onClick={handleSubmit} loading={loading}>
         <Typography className="fw-20-500-24">{t('create')}</Typography> 
        </Button>
      </Stack>
    </Modal>
  );
};

export default Add;
