import React,{useState} from "react";
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack ,Typography} from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import JsonCreator from "../../components/UI/JsonCreator";
import RadioButtonGroup from "../../components/input/radio";
import crossIcon from '../../assets/svg/cross-icon.svg'
import { t } from "i18next";

const Add = ({ open, onClose, data, setData, onSubmit, loading,edit,assetsList}) => {

  const USER_SCHEMA = [
    {
      title: "Name",
      placeholder: "Enter Name",
      type: SCHEMA_TYPES.INPUT,
      name: "name",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Asset",
      placeholder: "Select Assests",
      type: SCHEMA_TYPES.SELECT,
      name: "assets",
      multi:true,
      validator: VALIDATORS.REQUIRED,
      keyValues:assetsList
    },
    {
      title: "Threshold (seconds)",
      placeholder: "Enter Name",
      type: SCHEMA_TYPES.INPUT,
      name: "timeLimit",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title:"Type",
      type: SCHEMA_TYPES.RADIO,
      name:"type",
      keyValues:[{"value":false,"label":"Productive"},{"value":true,"label":"Non-Productive"}]
    },
   
   
  ];
  const [errors, setErrors] = useState(new Map());

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

    const handleSubmit=async ()=>{
          const _errors = Validate(USER_SCHEMA, data);
          setErrors(_errors);
          if (_errors.size) return;
          await onSubmit()
          return;
    }
  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(700px,90vw)"
      hideCloseIcon={crossIcon}
      wrapperClasses="px-6"
    >
      <h3 className="opacity-80">{edit=== true?"EDIT":"ADD"} DOWNTIME REASON</h3>
      
      <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={USER_SCHEMA}
        />
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          {t('cancel')}
        </Button>
        <Button color={'light_green'} onClick={handleSubmit} loading={loading}>
         <Typography className="fw-20-500-24">{t('create')}</Typography> 
        </Button>
      </Stack>
    </Modal>
  );
};

export default Add;
