import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Table from "../../components/table";
import useAsset from "../../store/asset";
import Button from "../../components/button";
import { toast } from "react-toastify";
import { IconButton, Stack, Typography,Grid } from "@mui/material";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { Delete,Checklist } from "@mui/icons-material";
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import EditIcon from '@mui/icons-material/Edit';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import Category from "../../components/UI/Category";
import { COLORS } from "../../helpers/constants";
import AddAsset from "./AddAsset";
import JsonFormatter from "../../components/UI/JsonFormatter";
import moment from "moment";


const AssetPage = () => {
  const location = useLocation()
  const navigate = useNavigate()
  const divId = location.state
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(false)
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
    isEndLine: "",
    PerformanceCal:'',
    fixed_capacity:''
  });
  const { list, create,update, remove } = useAsset();

  useEffect(() => {
    fetchCompany();
  }, [page]);

  const fetchCompany = async () => {
    setLoading("fetch");
    const [_list, _count] = await list(page + 1,divId);
    setUsers(_list);
    setTotal(_count);
    setLoading(false);
  };

  const handleAdd = async () => { 
    const payload = {...data} 
    // let req={
    //   "name": payload.alias,
    //   "api_url": payload.api_endpoint,
    //   "device_id": payload.machineID,
    //   "serial_number": payload.snumber,
    //   "machine_model": payload.model,
    //   "is_end_of_line": payload.isEndLine==='true'?true:false,
    //   "show_parts_per_second":payload.show_parts,
    //   "time_based_utilization": payload.PerformanceCal === 'true'?true:false,
    //   "fixed_capacity":payload.PerformanceCal==='true'? payload.fixed_capacity:false,
    //   "default_cycle_time":payload.PerformanceCal==='true' && payload.fixed_capacity==='true'? Number(payload.cycle_time):'',
    //   "default_production_per_hour": payload.PerformanceCal==='false'?payload.productionTime:''
    // }

    let req = {
      name: payload.alias,
      api_url: payload.api_endpoint,
      gridx_id:payload.uuid,
      device_id: payload.machineID,
      serial_number: payload.snumber,
      machine_model: payload.model,
      is_end_of_line: payload.isEndLine === 'true',
      show_parts_per_second: payload.show_parts,
      time_based_utilization: payload.PerformanceCal === 'true',
      fixed_capacity: payload.PerformanceCal === 'true' ? payload.fixed_capacity === 'true' : false,
      default_cycle_time:
        payload.PerformanceCal === 'true' && payload.fixed_capacity === 'true'
          ? parseInt(payload.cycle_time, 10) || 0
          : 0,
      default_production_per_hour: 
        payload.PerformanceCal === 'false' 
          ? parseFloat(payload.productionTime) || 0
          : 0,
    };
    
    console.log("machine ---payload",req)
    // return
    setLoading("fetch");
    let res = await create(req,divId);
    console.log("machine response",res)
    setData({});
    setModalOpen(false);
    await fetchCompany();
  };

  const handleDelete = async (id) => {
    setLoading("fetch");
    await remove(id);
    await fetchCompany();
    setLoading(false);
  };
  const handleUpdate = async (id,is_admin) => {
    setLoading("fetch");
    const payload={
      "is_admin":is_admin===true?false:true
    }
    await update(id,payload);
    await fetchCompany();
    setLoading(false);
  };

  const COLUMNS = [
    {
      id: 11,
      title: "Created At",
      render: (rowData) => moment(rowData.created).format('ll'),
    },
    {
      id: 1,
      title: "Asset Name",
      render: (rowData) => rowData.name,
    },
    {
      id: 2,
      title: "Line Name",
      render: (rowData) => rowData.line,
    },
    {
      id: 3,
      title: "Owner",
      render: (rowData) => rowData.owner,
    },
    // {
    //   id: 6,
    //   title: "Details",
    //   render: (rowData) => (
    //     <JsonFormatter
    //       icon={<Checklist fontSize="small" />}
    //       json={rowData?.metadata}
    //     />
    //   ),
    // },
    // {
    //   id: 5,
    //   title: "Actions",
    //   render: (rowData) => (
    //     <Stack flexDirection={'row'}>
    //     {/* {!rowData?.is_admin &&(
    //     <IconButton
    //       sx={{ height: 25, width: 25 }}
    //       onClick={() => handleUpdate(rowData?.id,rowData?.is_admin)}
    //     >
    //     <AdminPanelSettingsIcon color={rowData.is_admin?"error":"success"} fontSize="small" />
    //     </IconButton>
    //     )} */}
       
    //     <IconButton
    //     sx={{ height: 25, width: 25 }}
    //     onClick={()=>{setEdit(true);setModalOpen("add");setData(rowData)}}
    //   >
    //   <EditIcon color="primary" fontSize="small" />
    //   </IconButton>
    //   <IconButton
    //       sx={{ height: 25, width: 25 }}
    //       onClick={() => handleDelete(rowData?.id)}
    //     >
    //       <Delete color="error" fontSize="small" />
    //     </IconButton>
    //   </Stack>
    //   ),
    // },
  ];

  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
      <Stack sx={{flexDirection:'row',alignItems:'center',gap:1}}>
      <IconButton onClick={()=>navigate(-1)}>
          <ArrowBackIcon/>
        </IconButton>
        <Typography className="fw-20-500-36">
          Assets
          {/* <span className="fs-30 ml-1 c-gray">({users.length})</span> */}
        </Typography>
        </Stack>
        <Button color={'gray'} onClick={() => setModalOpen("add")} loading={loading}>
          <Typography className="fw-20-500-24">
          Add Asset
          </Typography> 
        </Button>
      </div>
      <Table
        columns={COLUMNS}
        rows={users}
        rowsPerPage={5}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
      />
      <AddAsset
        open={modalOpen === "add"}
        onClose={() =>{ setModalOpen(false);setEdit(false)}}
        data={data}
        edit={edit}
        setData={setData}
        onSubmit={handleAdd}
      />
    </div>
  );
};

export default AssetPage;
