import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Table from "../../components/table";
import useDowntime from "../../store/downtimeMaster";
import Button from "../../components/button";
import Add from "./Add";
import { toast } from "react-toastify";
import { IconButton, Stack, Typography,Grid } from "@mui/material";
import { Delete ,Checklist} from "@mui/icons-material";
import JsonFormatter from "../../components/UI/JsonFormatter";
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import EditIcon from '@mui/icons-material/Edit';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import Category from "../../components/UI/Category";
import { COLORS } from "../../helpers/constants";

import moment from "moment";

const DowntimePage = () => {
  const navigate = useNavigate()
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(false)
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
    name: "",
    metadata:{}
  });
const [assets, setAssets] = useState([])
  const { list, create,update, remove,assetList } = useDowntime();

  useEffect(() => {
    fetchCompany();
  }, [page]);

  useEffect(() => {
    fetchAssets();
  }, []);

  const fetchCompany = async () => {
    setLoading("fetch");
    const [_list] = await list(page + 1);
    setUsers(_list.results);
    setTotal(_list.count);
    setLoading(false);
  };

  const fetchAssets = async () => {
    setLoading("fetch");
    const [_list] = await assetList(page + 1);
    console.log("assets linst",_list)
    const newUsers = _list.map(item => ({
      value: `${item.owner} > ${item.line} > ${item.name}`,
      key: item.id
    }));
    setAssets(prevUsers => [...prevUsers,...newUsers]);
  
  };

  const handleAdd = async () => { 
    const payload = {...data} 
    console.log("payload",payload)
    const req ={
      reason_name:payload.name,
      effective_downtime:payload.type,
      time_limit:payload.timeLimit,
      asset_list:payload.assets
    }
    setLoading("fetch");
    let res =  await create(req);
    setData({});
    setModalOpen(false);
    await fetchCompany();
    console.log("creat res----",res)
  };

  const handleDelete = async (id) => {
    setLoading("fetch");
    await remove(id);
    await fetchCompany();
    setLoading(false);
  };
  const handleUpdate = async (id,is_admin) => {
    setLoading("fetch");
    const payload={
      "is_admin":is_admin===true?false:true
    }
    await update(id,payload);
    await fetchCompany();
    setLoading(false);
  };

  const SEARCH = [
    {
      id: 'name',
      title: "Name"
    },
    {
      id: 'sap_id',
      title: "SAP ID",
    },
    {
      id: 'hsn_code',
      title: "HSN CODE"
    }, 
    {
      id: 'internal_id',
      title: "Internal ID",
    },
    {
      id: 'serialization',
      title: "Serialization",
    },
    {
      id: 'category',
      title: "Category",
    },  
   
  ];

  const COLUMNS = [
    {
      id: 1,
      title: "Name",
      render: (rowData) => <Typography sx={{cursor:'pointer',textTransform:'capitalize'}} >{rowData.reason_name}</Typography>,
    },
    {
      id: 2,
      title: "Effective Downtime",
      render: (rowData) => <Typography >{rowData.effective_downtime?"True":"False"}</Typography>
    },
    // {
    //   id: 3,
    //   title: "Created By",
    //   render: (rowData) => <Typography color='primary' sx={{cursor:'pointer'}} >{rowData.created}</Typography>,
    // },
    {
      id: 4,
      title: "Created on",
      render: (rowData) => <Typography >{moment(rowData.created).format('ll')}</Typography>,
    },
    {
      id: 7,
      title: "Status",
      render: (rowData) => <Typography >{rowData.status?"True":"False"}</Typography>,
    },
    {
      id: 8,
      title: "Lines",
      render: (rowData) => 
        <Stack>
      {/* <Grid display={'grid'} gridTemplateColumns={{ sm: 'repeat(2, 1fr)', xs: 'repeat(2, 1fr)' }}> */}
      {/* <Typography >{rowData.applicable_to.map((item)=>item.name)}</Typography>, */}
      {rowData.applicable_to.map((item,index)=>(<Typography>
        {item.name}
        {index !== rowData.applicable_to.length - 1 && ", "}</Typography>))}
      {/* </Grid> */}
      </Stack>
    },
    {
      id: 2,
      title: "Threshold",
      render: (rowData) => <Typography >{rowData.time_limit}</Typography>,
    },
    // {
    //   id: 5,
    //   title: "Action",
    //   render: (rowData) => (
    //     <Stack flexDirection={'row'}> 
    //     <IconButton
    //     sx={{ height: 25, width: 25 }}
    //     onClick={()=>{setData(rowData);setEdit(true);setModalOpen("add")}}
    //   >
    //   <EditIcon color="primary" fontSize="small" />
    //   </IconButton>
    //   </Stack>
    //   ),
    // },
  ];



  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
          <Typography className="fw-20-500-36">
         Downtime Master
         {/* <span className="fs-30 ml-1 c-gray">({users.length})</span> */}
        </Typography>
        <Stack sx={{flexDirection:"row",gap:1}}>
        <Button color={'gray'} onClick={() => setModalOpen("add")} loading={loading}>
          <Typography className="fw-20-500-24">
          Add Downtime Reason
          </Typography> 
        </Button>
        </Stack>
      </div>
     
      <Table
        columns={COLUMNS}
        rows={users}
        rowsPerPage={10}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
      />
      <Add
        open={modalOpen === "add"}
        onClose={() =>{ setModalOpen(false);setEdit(false)}}
        data={data}
        edit={edit}
        setData={setData}
        onSubmit={handleAdd}
        assetsList={assets}
      />
    </div>
  );
};

export default DowntimePage;
