import React from "react";
import { resolveResponse } from "../api/utils";
import REQUEST, { CONTENT_TYPES } from "../api";
import { ENDPOINTS } from "../api/endpoints";
import { useSelector } from "react-redux";
import { REDUCER_TYPES } from "..";

const useDowntime = () => {
  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);

  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };

  const list = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `settings/downtime-reasons/?page=${page}&page_size=10`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const all_downtime_list = async (date) => {
    const client = new REQUEST();
    const res = await client.get(
      `monitoring/all-downtime-list/${date}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const unassi_downtime_list = async (date) => {
    const client = new REQUEST();
    const res = await client.get(
      `monitoring/unassigned-downtime-list/${date}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const reason_list = async (packetId) => {
    const client = new REQUEST();
    const res = await client.get(
      `monitoring/downtime-reasons/${packetId}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const post_reason = async (packetId,payload = {}) => {
    const client = new REQUEST();
    const res = await client.patch(
      `monitoring/downtime-assign/${packetId}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };
  

  const assetList = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `asset/all-assets/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const create = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `settings/downtime-reasons/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };

  const update = async (payload) => {
    const newPayload ={
      "name":payload.name,
      "metadata":payload.metadata
    }
    const client = new REQUEST();
    const res = await client.put(
      `${ENDPOINTS.ASSET}/${ENDPOINTS.COMPANY}/${payload.id}/`,
      newPayload,
      headers,
      (res) => resolveResponse(res, null, "Company Updated successfully!")
    );
    return res?.data;
  };

  const remove = async (id) => {
    const client = new REQUEST();
    const res = await client.delete(
      `${ENDPOINTS.ASSET}/${ENDPOINTS.COMPANY}/${id}/`,
      headers,
      (res) => resolveResponse(res, null, "Company deleted successfully!")
    );
    return res?.data;
  };

  return {
    list,
    assetList,
    create,
    update,
    remove,
    all_downtime_list,
    unassi_downtime_list,
    reason_list,
    post_reason
  };
};

export default useDowntime;
