import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Table from "../../components/table";
import useShift from "../../store/shiftMaster";
import Button from "../../components/button";
import Add from "./Add";
import { toast } from "react-toastify";
import { IconButton, Stack, Typography,Grid } from "@mui/material";
import { Delete ,Checklist} from "@mui/icons-material";
import JsonFormatter from "../../components/UI/JsonFormatter";
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import EditIcon from '@mui/icons-material/Edit';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import Category from "../../components/UI/Category";
import { COLORS } from "../../helpers/constants";
import Select from "../../components/input/Select";
import Input from "../../components/input";
import CircleIcon from '@mui/icons-material/Circle';
import moment from "moment/moment";

const ShiftMaster = () => {
  const navigate = useNavigate()
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(false)
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
    name: "",
    metadata:{}
  });
  const [shift , setShift] = useState([])
  const [masterShift, setMasterShift] = useState([])
  const [plants, setPlants] = useState([])
  const [days, setDays] = useState({
    monday: false,
    tuesday: false,
    wednesday: false,
    thursday: false,
    friday: false,
    saturday: false,
    sunday: false,
  });
  const { list, create,update, remove ,plantList} = useShift();

  useEffect(() => {
    fetchCompany();
  }, [page]);

  useEffect(()=>{
    fetchPlants()
  },[])

  const fetchCompany = async () => {
    setLoading("fetch");
    const [_list] = await list(page + 1);
    setShift(_list.results)
    setTotal(_list.count)
    setLoading(false);
    setMasterShift([])
    const newUsers = _list.results.map(item => ({
      value: item.shift_name,
      key: item.id
    }));
    setMasterShift(prevUsers => [...prevUsers,...newUsers])
  };

  const fetchPlants =async ()=>{
    const [_list, _count] = await plantList(page + 1);
    console.log("plants---",_list)
    setPlants([])
    const newUsers = _list.map(item => ({
      value: item.name,
      key: item.id
    }));
    setPlants(prevUsers => [...prevUsers,...newUsers]);
  }

  const handleAdd = async () => { 
    const payload = {...data} 
    const req ={
      shift_name:payload.name,
      plant_code:payload.plant,
      start_time:payload.start_time,
      end_time:payload.end_time,
      is_shift:payload.type,
      master_shift_id:payload.swiftmaster===0?'':payload.swiftmaster,
      ...days
    }
    console.log("shift final payload",req)
    // return
    setLoading("fetch");
    let res = await create(req);
    setData({});
    setModalOpen(false);
    await fetchCompany();
  };

  const handleDelete = async (id) => {
    setLoading("fetch");
    await remove(id);
    await fetchCompany();
    setLoading(false);
  };
  const handleUpdate = async (id,is_admin) => {
    setLoading("fetch");
    const payload={
      "is_admin":is_admin===true?false:true
    }
    await update(id,payload);
    await fetchCompany();
    setLoading(false);
  };

  const SEARCH = [
    {
      id: 'plant',
      title: "Plant"
    },
    {
      id: 'name',
      title: "Name",
    },
    {
      id: 'type',
      title: "Type"
    }, 
    {
      id: 'duration',
      title: "Duration",
    },
    {
      id: 'start',
      title: "Start",
    },
    {
      id: 'end',
      title: "End",
    },  
    {
      id: 'mon',
      title: "Mon",
    }, 
    {
      id: 'tue',
      title: "Tue",
    }, 
    {
      id: 'wed',
      title: "Wed",
    },
    {
      id: 'thu',
      title: "Thu",
    },  
    {
      id: 'fri',
      title: "Fri",
    },   
    {
      id: 'sat',
      title: "Sat",
    }, 
    {
      id: 'sun',
      title: "Sun",
    }
  ];
  const COLUMNS = [
    {
      id: 1,
      title: "Plant",
      render: (rowData) => <Typography >{rowData.plant.name}</Typography>,
    },
    {
      id: 2,
      title: "Name",
      render: (rowData) => <Typography sx={{textTransform:'capitalize'}} >{rowData.shift_name}</Typography>
    },
    {
      id: 3,
      title: "Type",
      render: (rowData) => <Typography >{rowData.is_shift?'Working Shift':'Planned Break'}</Typography>,
    },
    {
      id: 4,
      title: "Duration",
      render: (rowData) => 
          // <Typography>{rowData.duration/60} min</Typography>
          <Typography> {moment.utc(rowData.duration * 1000).format("H [hrs] mm [mins]")}</Typography>
          
    },
    {
      id: 5,
      title: "Start",
      render: (rowData) => <Typography >{rowData.start_time}</Typography>,
    },
    {
      id: 6,
      title: "End",
      render: (rowData) => <Typography >{rowData.end_time}</Typography>,
    },
    {
      id: 7,
      title: "Mon",
      render: (rowData) =><CircleIcon color={rowData.monday?"success":'error'}/>,
    },
    {
      id: 8,
      title: "Tue",
      render: (rowData) => <CircleIcon color={rowData.tuesday?"success":'error'}/>,
    },
    {
      id: 9,
      title: "Wed",
      render: (rowData) => <CircleIcon color={rowData.wednesday?"success":'error'}/>,
    },
    {
      id: 10,
      title: "Thu",
      render: (rowData) => <CircleIcon color={rowData.thursday?"success":'error'}/>,
    },
    {
      id: 11,
      title: "Fri",
      render: (rowData) => <CircleIcon color={rowData.friday?"success":'error'}/>,
    },
    {
      id: 12,
      title: "Sat",
      render: (rowData) => <CircleIcon color={rowData.saturday?"success":'error'}/>,
    },
    {
      id: 13,
      title: "Sun",
      render: (rowData) => <CircleIcon color={rowData.sunday?"success":'error'}/>,
    },
    // {
    //   id: 14,
    //   title: "Action",
    //   render: (rowData) => (
    //     <Stack flexDirection={'row'}> 
    //     <IconButton
    //     sx={{ height: 25, width: 25 }}
    //     onClick={()=>{setData(rowData);setEdit(true);setModalOpen("add")}}
    //   >
    //   <EditIcon color="primary" fontSize="small" />
    //   </IconButton>
    //   </Stack>
    //   ),
    // },
  ];

  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
         <Typography className="fw-20-500-36">
         Shift Master
         {/* <span className="fs-30 ml-1 c-gray">({users.length})</span> */}
        </Typography>
        <Stack sx={{flexDirection:"row",gap:1}}>
        <Button color={'gray'} onClick={() => setModalOpen("add")} loading={loading}>
          <Typography className="fw-20-500-24">
          Add Shift Schedule
          </Typography> 
        </Button>
        </Stack>
      </div>
      {/* <Stack direction="row" justifyContent="flex-end" sx={{mb:1}}>
          <Select
            keyValues={SEARCH.map((d) => ({
              key: d?.id,
              value: d?.title,
            }))}
            width="120px"
            placeholder="Search by"
            classes="mr-1"
            // value={searchBy}
            // onChange={(e) => setSearchBy(e.target.value)}
          />
          <Input
            height="37px"
            placeholder="Universal Search"
            // onChange={(e)=>{setSearchInput(e.target.value);sList(e.target.value)}}
          />
        </Stack> */}
      <Table
        columns={COLUMNS}
        rows={shift}
        rowsPerPage={10}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
      />
      <Add
        open={modalOpen === "add"}
        onClose={() =>{ setModalOpen(false);setEdit(false)}}
        data={data}
        edit={edit}
        setData={setData}
        onSubmit={handleAdd}
        plantList={plants}
        days={days}
        setDays={setDays}
        masterShift={masterShift}
      />
    </div>
  );
};

export default ShiftMaster;
